<template>
  <div>
    <transition
      :name="transition"
      mode="in-out"
    >
      <slot></slot>
    </transition>
  </div>
</template>

<script>
export default {
  props: ["name"],
  data() {
    return {
      transition: "fade"
    };
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      this.transition = to.meta.transition
        ? to.meta.transition
        : this.$props.name;
      next();
    });
  }
};
</script>

<style lang="scss">
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.75s linear;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateX(1000px);
}
</style>    